@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css');

* {
  box-sizing: border-box;
}

.about-main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  min-height: 700px;
  padding: 30px;
  background-image: url('../assets/background-aboutPage.jpg');
  background-size: cover;
  background-position: center;

  *:not(i) {
    font-family: 'Raleway', sans-serif;
  }
  
  .text-content {
    font-family: 'Comic Neue', sans-serif;
    font-weight: 700;
  }
}

// Header
.about-header {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  margin-top: 10px;
  margin-bottom: 0px;
  background-color: rgba(0,0,0,0.7);
}

.about-main-heading {
  flex-grow: 1;
  text-align: center;
  color: white;
  text-shadow: 3px 3px black;
  font-size: 75px;
}

.about-back-button {
  justify-self: flex-start;
  text-decoration: none;
  color: white;
  text-shadow: 2px 2px black;
}

.about-back-button:hover {
  color: orange
}

// Notebook
.about-notebook {
  display: flex;
  width: 1000px;
  height: 70%;
  border: 5px solid orange;
  border-radius: 30px;
  box-shadow: 0 0 10px 5px;
  margin: auto 0;
}

// Aside
.about-aside {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 30%;
  height: 100%;
  min-height: fit-content;
  padding: 10px;
  border-radius: 25px 0 0 25px;
  background-color: rgba(0,0,0,0.5);
  
  button {
    height: 175px;
    width: 175px;
    background-color: transparent;
    padding: 0px;
    border: 2px solid white;
    border-radius: 100%;
    margin: 10px 0;
    color: white;
    font-size: 25px;
    text-align: center;
    cursor: pointer;
  }

  button:focus {
    outline: none;
  }

  button:not(.button-active):hover {
    background-color: dodgerblue;
    font-weight: 800;
    text-shadow: 2px 2px navy;
  }

  .button-active {
    background-color: orange;
    font-weight: 700;
    text-shadow: 2px 2px navy;
    cursor: auto;
  }
}

// Main
.about-section-container {
  width: 70%;
  height: 100%;
}

// About Sections
.about-section {
  height: 100%;
}

// Paper Effect 
.paper {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: white;
  box-shadow: 0px 0px 5px 0px #888;
  border-radius: 0 25px 25px 0;
}

.paper-pattern {
  height: 100%;
  background-image: repeating-linear-gradient(white 0px, white 24px, teal 25px);
}

.text-content {
  padding-top: 6px;
  padding-left: 56px;
  padding-right: 16px;
  line-height: 25px;
  font-size: 19px;
  letter-spacing: 1px;
  word-spacing: 5px;
  color: black;
  text-align: center;
}

.paper::before {
  content: '';
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 40px;
  background-color: rgba(255,0,0,0.6);
}

// Media Queries
@media screen and (max-width: 1080px) {
  .about-notebook {
    display: flex;
    flex-direction: column;
    width: 95%;
    border-radius: 30px 30px 0px 0px;
  }

  .about-aside {
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 40%;
    border-radius: 25px 25px 0 0;

    button {
      height: 150px;
      width: 150px;
      font-size: 20px;
      text-align: center;
    }
  }

  .paper {
    padding-top: 30px;
    padding-bottom: 20px;
    border-radius: 0px;
    flex-grow: 1;
  }

  .paper-pattern {
    border-radius: 0px;
  }

  .about-section-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow-y: scroll;
  }

  .about-section {
    flex-grow: 1;
    height: unset;
    border-radius: 0px;
  }
}

@media screen and (max-width: 765px) {
  .about-aside {
    height: 35%;
    padding: 5px;

    button {
      height: 60px;
      width: 120px;
      font-size: 17px;
      border-radius: 10px;
    }

    .button-active {
      text-shadow: 1px 1px black;
    }
  }
}

@media screen and (max-width: 550px) {
  .about-header {
    margin-top: 20px;

    .about-main-heading {
      font-size: 50px;
    }

    i {
      font-size: 45px;
    }
  }
}

@media screen and (max-width: 495px) {
  .about-aside {
    height: 20%;

    button {
      width: 30%;
      height: 40px;
      font-size: 3vw;
    }
  }

  .about-header {
    padding: 5px 10px;
    .about-main-heading {
      font-size: 40px;
    }

    i {
      font-size: 35px;
    }
  }
}

@media screen and (orientation: landscape) {
  .about-main-container {
    min-height: 700px;
  }
}

@media screen and (orientation: portrait) {
  .about-main-container {
    min-height: 500px;
  }
}
