@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');

* {
  box-sizing: border-box;
}

.title-main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('../assets/background-homePage.gif');
  background-size: cover;
  background-position: center;
}

.title-sub-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main-heading {
  font-family: 'Audiowide', sans-serif;
  text-transform: uppercase;
  font-weight: 100;
  font-size: 15vw;
  text-shadow: 7px 0px dodgerblue;
  text-align: center;
  color: cyan;
}

.start-link {
  font-family: 'Audiowide', sans-serif;
  text-transform: uppercase;
  font-weight: 900;
  text-decoration: none;
  font-size: 8vw;
  color: yellow;
  text-shadow: 5px 1px orangered;
}

.start-link:hover {
  color: lime;
  text-shadow: 5px 1px green;
  font-weight: 100;
  font-style: italic;
}

@media screen and (max-width: 550px) {
  .main-heading {
    text-shadow: 3px 0px dodgerblue;
  }

  .start-link {
    text-shadow: 2px 1px orangered;
  }
}


