@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
}

.home-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 750px;
  padding: 20px;
  background-image: url('../assets/background-homePage.gif');
  background-size: cover;
  background-position: center;

  *:not(i) {
    // color: white;
    // text-shadow: 2px 2px black;
    font-family: 'Comfortaa', sans-serif;
    font-weight: 700;
    font-style: italic;
  }
}

// Headings
.home-headings {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 5px;

  hr {
    width: 90%;
    color: lightgrey;
    font-weight: 300;
    margin-bottom: 10px;
    text-align: center;
  }
}

.home-main-heading {
  font-weight: bold;
  font-size: 75px;
  text-align: center;
  color: cyan;
  text-shadow: 4px -1px dodgerblue, 0 0 10px blue, 0 0 20px blue, 0 0 30px blue, 0 0 40px blue;
}

.home-sub-heading {
  font-weight: 900;
  font-size: 30px;
  font-style: italic;
  text-align: center;
  color: yellow;
  text-shadow: 2px 0px orangered, 0 0 10px red, 0 0 20px red, 0 0 30px red, 0 0 40px red;
}

// App Icons
.home-app-icons {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 100%;
  padding: 15px 0;
  margin-top: 10px;
}

.home-icon-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 10px 10px;

  Link {
    text-decoration: none;
    color: none;
  }
}

.home-app-icon {
  height: 200px;
  width: 200px;
  border: 1px solid white;
  border-radius: 25px;
  background-color: dodgerblue;
  box-shadow: 0px 0px 5px 2px black;
  background-size: cover;
  background-position: center;
}

.home-app-icon#about {
  background-image: url("../assets/app-icon-about.png");
}

.home-app-icon#experience {
  background-image: url("../assets/app-icon-experience.jpg");
}

.home-app-icon#projects {
  background-image: url("../assets/app-icon-projects.jpg");
}

.home-app-icon#about:hover {
  box-shadow: 0px 0px 10px 5px orange;
  border: 3px solid yellow;
}

.home-app-icon#experience:hover {
  box-shadow: 0px 0px 10px 5px lime;
  border: 3px solid green;
}

.home-app-icon:hover {
  box-shadow: 0px 0px 10px 5px dodgerblue;
  border: 3px solid cyan;
}

.home-app-name {
  font-size: 25px;
  font-weight: 300;
  font-style: italic;
  color: white;
  text-shadow: 2px 2px black;
}

// Footer
.home-contact-info {
  margin-top: 75px;
  display: flex;
  flex-direction: column;
  background-color: rgba(0,0,0,0.7);
  padding: 20px;
  border-radius: 25px;
}

.home-contact-info-heading {
  font-weight: bold;
  font-size: 50px;
  text-align: center;
  margin-bottom: 15px;
  color: white;
  text-shadow: 2px 2px black;
}

.home-contact-icons {
  display: flex;
  justify-content: space-evenly;

  i {
    margin: 0px 10px;
    text-shadow: none;
  }

  i.fa-linkedin-in {
    color: #0e76a8;
  }

  i.fa-linkedin-in:hover {
    color: lightskyblue;
  }

  i.fa-envelope {
    color: #B23121;
  }

  i.fa-envelope:hover {
    color: coral;
  }

  i.fa-github-alt {
    color: #6e5494;
  }

  i.fa-github-alt:hover {
    color: plum;
  }

  i.fa-twitter {
    color: #1DA1F2;
  }

  i.fa-twitter:hover {
    color: deepskyblue;
  }
}

// Media Queries
@media screen and (max-width: 700px) {
  .home-main-container {
    padding: 20px;
  }

  .home-main-heading {
    font-size: 60px;
  }

  .home-sub-heading {
    font-size: 20px;
  }

  .home-contact-info-heading {
    font-size: 40px;
  }

  .home-app-name {
    font-size: 20px;
  }

  .home-app-icon {
    height: 150px;
    width: 150px;
  }
}

@media screen and (max-width: 550px) {
  .home-main-heading {
    font-size: 50px;
  }

  .home-sub-heading {
    font-size: 18px;
    text-shadow: 1px 1px solid black;
  }

  .home-app-icon {
    height: 100px;
    width: 100px;
    border-radius: 15px;
  }

  .home-contact-info {
    margin-top: 40px;

    i {
      font-size: 50px;
    }

    .home-contact-info-heading {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 418px) {
  .home-contact-info {
    margin-top: 10px;
  }
}