@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
}

.exp-main-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100vh;
  min-height: 700px;

  *:not(i) {
    font-family: 'Raleway', sans-serif;
  }
}

// Header
.exp-header {
  display: flex;
  align-items: center;
  height: 10vh;
  min-height: fit-content;
  width: 100%;
  background-color: #244747;
  padding: 10px 20px;
  color: white;

  h1 {
    flex-grow: 1;
    text-align: center;
    font-weight: 900;
    font-size: 55px;
  }

  .exp-back-button {
    justify-self: flex-start;
    text-decoration: none;
    color: white;
  }

  .exp-back-button:hover {
    color: orange;
  }
}
// Caroursel
.exp-carousel-page {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  height: 40vh;
  min-height: 225px;
  background-size: cover;
  background-position: center;
  padding-bottom: 50px;

  .page-legend {
    padding: 10px;
    width: 30%;
    background-color: rgba(0,0,0, 0.5);
    opacity: 1;

    p {
      color: white;
      text-shadow: 2px 2px black;
    }
  }
}

// Experience Info
.exp-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  padding: 20px;
  min-height: fit-content;
  background-color: #CACCCE;

  .text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: fit-content;
    padding: 20px;
    text-align: center;
    color: white;
    background-color: rgba(0,0,0, 0.5);
    text-shadow: 2px 2px black;
    box-shadow: 0px 5px 10px 1px black;

    h1 {
      font-size: 85px;
      margin-bottom: 10px;
    }

    h2 {
      font-size: 30px;
    }

    p {
      font-size: 32px;
    }
  }

  .skill-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 95%;
    height: fit-content;
    padding: 15px;
    text-align: center;
    color: white;
    background-color: rgba(0,0,0, 0.5);
    box-shadow: 0px 5px 10px 1px black;
    text-shadow: 2px 2px black;

    h1 {
      font-size: 85px;
    }

    .skill-icons {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      width: 100%;
      height: fit-content;
      padding: 10px;
      border: 1px solid cyan;
      border-radius: 10px;

      .skill-icon-container {
        display: flex;
        flex-direction: column;
        margin: 10px;
        text-shadow: 2px 2px black;
        
        i {
          margin-bottom: 5px;
          font-size: 70px;
        }
      }
    }
  }
}

// Media Queries
@media screen and (orientation: landscape) {
  .exp-main-container {
    min-height: 700px;
  }
}

@media screen and (orientation: portrait) {
  .exp-main-container {
    min-height: 700px;
  }
}

@media screen and (max-width: 1160px) {
  .exp-info {

    .text-container {
      h1 {
        font-size: 75px;
      }
  
      h2 {
        font-size: 25px;
      }
  
      p {
        font-size: 28px;
      }
    }
  
    .skill-container {
      h1 {
        font-size: 75px;
      }
    }
  }  
}

@media screen and (max-width: 1005px) {
  .exp-info {

    .text-container {
      h1 {
        font-size: 70px;
      }
  
      h2 {
        font-size: 23px;
      }
  
      p {
        font-size: 25px;
      }
    }
  
    .skill-container {
      h1 {
        font-size: 70px;
      }

      .skill-icons {
        .skill-icon-container {
          i {
            font-size: 70px;
          }
        }
      }
    }
  }  
}

@media screen and (max-width: 935px) {
  .exp-info {

    .skill-container {
      .skill-icons {
        h1 {
          font-size: 60px;
        }

        .skill-icon-container {
          i {
            font-size: 60px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 895px) {
  .exp-info {

    .text-container {
      h1 {
        font-size: 60px;
      }

      h2 {
        font-size: 20px;
      }

      p {
        font-size: 23px;
      }
    }

    .skill-container {
      h1 {
        font-size: 50px;
      }

      .skill-icons {

        .skill-icon-container {
          i {
            font-size: 50px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 865px) {
  .exp-info {

    .text-container {
      padding: 30px;
      width: 100%;
      height: 100%;
      border-radius: 0px;

      h1 {
        font-size: 55px;
      }

      h2 {
        font-size: 18px;
      }

      p {
        font-size: 20px;
      }
    }

    .skill-container {
      padding: 30px;
      justify-content: center;
      width: 100%;
      height: 100%;
      border-radius: 0px;
    }
  }
}

@media screen and (max-width: 600px) {
  .exp-info {

    .text-container {
      h1 {
        font-size: 50px;
      }

      h2 {
        font-size: 17px;
      }

      p {
        font-size: 20px;
      }
    }

    .skill-container {
      h1 {
        font-size: 50px;
      }

      .skill-icons {

        .skill-icon-container {
          i {
            font-size: 40px;
          }

          p {
            font-size: 12px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 505px) {
  .exp-header {
    i {
      font-size: 50px;
    }

    .exp-main-heading {
      font-size: 50px;
    }
  }

  .exp-info {
    overflow-y: scroll;

    .text-container {
      padding: 10px;

      h1 {
        font-size: 50px;
      }

      h2 {
        font-size: 17px;
      }

      p {
        font-size: 20px;
      }
    }

    .skill-container {
      padding: 10px;
      
  
      h1 {
        font-size: 50px;
      }

      .skill-icons {
        height: 280px;
        overflow-y: scroll;
        background-color: rgba(0,0,0,0.6);

        .skill-icon-container {
          height: fit-content;
          width: 25%;

          i {
            font-size: 70px;
          }
  
          p {
            font-size: 15px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .exp-header {
    i {
      font-size: 40px;
    }

    .exp-main-heading {
      font-size: 40px;
    }
  }

  .exp-info {

    .text-container {
      padding: 20px;

      h1 {
        font-size: 40px;
      }

      h2 {
        font-size: 15px;
      }

      p {
        font-size: 17px;
      }
    }

    .skill-container {
      h1 {
        font-size: 40px;
      }

      .skill-icons {
        width: 80%;

        .skill-icon-container {
          width: 30%;

          i {
            font-size: 80px;
          }

          p {
            font-size: 15px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 350px) {
  .exp-header {
    i {
      font-size: 30px;
    }

    .exp-main-heading {
      font-size: 30px;
    }
  }

  .exp-info {

    .text-container {
      padding: 30px;

      h1 {
        font-size: 35px;
      }

      h2 {
        font-size: 12px;
      }

      p {
        font-size: 15px;
      }
    }

    .skill-container {
      .skill-icons {
        .skill-icon-container {
          width: 50%;
        }
      }
    }
  }
}
